	data() {
		return {
			validApply: true,
			goodsList: []
		};
	},
	onLoad(option) {
		uni.showLoading({ title: '加载中' });
		uni.request({
			url: this.$tempUrl + 'wechat/getOpenId',
			data: { code: window.location.search.substr(6, 32) },
			method: 'GET',
			success: res => {
				const openId = res.data;
				uni.request({
					url: this.$tempUrl + 'seller/login',
					data: { openId: openId },
					method: 'GET',
					success: res => {
						if (res.data.code === 0) {
							uni.setStorageSync('seller', res.data.data);
							setTimeout(() => {
								this.refresh();
							}, 500);
						} else {
							uni.hideLoading();
							uni.showModal({
								title: '提示',
								content: '抱歉，您不是商家哦=。=',
								showCancel: false,
								success: res => {
									if (res.confirm) {
										console.log('用户点击确定');
										this.validApply = false;
									}
								}
							});
						}
					}
				});
			}
		});
		// uni.setNavigationBarTitle({
		// 	title: '订单列表'
		// });
	},
	onPullDownRefresh() {
		console.log('refresh');
		this.refresh();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 500);
	},
	methods: {
		refresh() {
			uni.request({
				url: this.$tempUrl + 'seller/order/list',
				data: { shopId: uni.getStorageSync('seller').shopId, token: uni.getStorageSync('seller').accessToken },
				method: 'GET',
				success: res => {
					uni.hideLoading();
					// uni.showToast({ title: 'success', icon: 'none' });
					this.goodsList = res.data.data;
					if (res.data.data.length === 0) {
						uni.showModal({
							title: '提示',
							content: '暂无订单哦~',
							showCancel: false
						});
					}
				}
			});
		},
		changeOrderStatus(goods) {
			if (goods.orderStatus === 1) {
				uni.showModal({
					title: '提示',
					content: '确认接受此订单？',
					success: res => {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.request({
								url: this.$tempUrl + 'seller/order/receive?token=' + uni.getStorageSync('seller').accessToken + '&orderId=' + goods.id,
								// data: { orderId: goods.id },
								method: 'POST',
								success: res => {
									uni.startPullDownRefresh();
									if (res.data.code === 0) {
										uni.showToast({ title: '接受成功，请尽快配送', icon: 'success' });
									} else {
										uni.showModal({
											title: '失败',
											content: '买家已取消此订单',
											showCancel: false
										});
									}
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		},
		// 拨打电话
		toCall(phoneNumber) {
			uni.showModal({
				title: '提示',
				content: '是否拨打客户电话？',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.makePhoneCall({
							phoneNumber: phoneNumber
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
};
